}

.programm-item__box .active-programm__banner {
  margin-bottom: 20px;
}

.programm-item__box .programm-img__box {
  width: 50%;
  float: left;
  margin: 0px 30px 20px 0px;
}

.programm-item__box .active-programm__content {
  padding: 0px;
